import React, { useState } from "react";

// styles
// needed asssets

// svgs

// images

// needed styles
import styles from "./index.module.scss";

import FsLightbox from "fslightbox-react";
import { v4 } from "uuid";

const TouristSingleResortLightBox = () => {
  const [lightboxController, setLightboxController] = useState({
    toggler: false,
    sourceIndex: 0,
  });
  function openLightboxOnSource(sourceIndex) {
    setLightboxController({
      toggler: !lightboxController.toggler,
      sourceIndex: sourceIndex,
    });
  }
  const images = [
    {
      id: Math.random(),
      src: "https://source.unsplash.com/random?resort",
    },
    {
      id: Math.random(),
      src: "https://source.unsplash.com/random?hotel",
    },
    {
      id: Math.random(),
      src: "https://source.unsplash.com/random?beach",
    },
    {
      id: Math.random(),
      src: "https://source.unsplash.com/random?beaches",
    },
    {
      id: Math.random(),
      src: "https://source.unsplash.com/random?land",
    },
    {
      id: Math.random(),
      src: "https://source.unsplash.com/random?sea",
    },
    {
      id: Math.random(),
      src: "https://source.unsplash.com/random?beach",
    },
  ];
  const sourcesImage = images?.map((image) => image?.src);
  return (
    <>
      <>
        <div className={styles["media-container"]}>
          {images?.map((image, index) => {
            return (
              <img
                key={v4()}
                onClick={() => openLightboxOnSource(index)}
                src={image?.src}
                alt="light-box"
              />
            );
          })}
          <FsLightbox
            toggler={lightboxController.toggler}
            sourceIndex={lightboxController.sourceIndex}
            sources={[...sourcesImage]}
          />
        </div>
      </>
    </>
  );
};

export default TouristSingleResortLightBox;
