import React from "react";

// styles
// import Rate from "rc-rate";
import "rc-rate/assets/index.css";
// needed asssets

// svgs
import { ReactComponent as LocationIcon } from "assets/svgs/location.svg";
// import { ReactComponent as HeartIcon } from "assets/svgs/heart.svg";
// import { ReactComponent as ShareIcon } from "assets/svgs/share.svg";

// images
// needed styles
import styles from "./index.module.scss";

const TouristSingleResortAside = () => {
  const data = {
    header: "استمتع بخدمة سهل ",
    paragraph: [
      {
        header: "مثال للاقامه المتميزه",
        paragraph: "موقع متميز مع تقيم عالي من نزلاء حديثين الاقامه",
      },
      {
        header: "مثال للاقامه المتميزه",
        paragraph: "موقع متميز مع تقيم عالي من نزلاء حديثين الاقامه",
      },
      {
        header: "مثال للاقامه المتميزه",
        paragraph: "موقع متميز مع تقيم عالي من نزلاء حديثين الاقامه",
      },
      {
        header: "مثال للاقامه المتميزه",
        paragraph: "موقع متميز مع تقيم عالي من نزلاء حديثين الاقامه",
      },
    ],
  };
  const svgData = [
    <LocationIcon />,
    <LocationIcon />,
    <LocationIcon />,
    <LocationIcon />,
  ];
  return (
    <div className={styles["container"]}>
      <h4>{data?.header}</h4>

      {data?.paragraph?.map((i, index) => (
        <div className={styles["info"]}>
          <div>{svgData[index]}</div>
          <div>
            <h6>{i.header}</h6>
            <p>{i.paragraph}</p>
          </div>
        </div>
      ))}
    </div>
  );
};

export default TouristSingleResortAside;
