import React from "react";

// styles
import Rate from "rc-rate";
import "rc-rate/assets/index.css";
// needed asssets

// svgs
import { ReactComponent as LocationIcon } from "assets/svgs/location.svg";
import { ReactComponent as HeartIcon } from "assets/svgs/heart.svg";
import { ReactComponent as ShareIcon } from "assets/svgs/share.svg";

// images
// needed styles
import styles from "./index.module.scss";

const TouristSingleResortHeader = () => {
  return (
    <div className={styles["container"]}>
      <div className={styles["info"]}>
        <div className={styles["info-header"]}>
          <h2>تروبيل سهل حشيش</h2>
          <span>
            <Rate value={5} className={styles["rate-stars"]} />
          </span>
        </div>
        <div className={styles["info-body"]}>
          <span>
            <LocationIcon />
          </span>
          <p> تروبيل سهل حشيش تروبيل سهل حشيش</p>
        </div>
      </div>
      <div className={styles["actions"]}>
        <HeartIcon />
        <ShareIcon />
        <button className={styles["button"]}>احجز الان</button>
      </div>
    </div>
  );
};

export default TouristSingleResortHeader;
