import React from "react";
import { Col, Container, Row } from "react-bootstrap";

// needed styles
import styles from "./index.module.scss";
import TouristSingleResortAside from "./TouristSingleResortAside/TouristSingleResortAside";
import TouristSingleResortDescription from "./TouristSingleResortDescription/TouristSingleResortDescription";
// needed components
import TouristSingleResortHeader from "./TouristSingleResortHeader/TouristSingleResortHeader";
import TouristSingleResortLightBox from "./TouristSingleResortLightBox/TouristSingleResortLightBox";
import TouristSingleResortMap from "./TouristSingleResortMap/TouristSingleResortMap";
const TouristSingleResortPage = () => {
  return (
    <div className={styles["container"]}>
      <Container>
        <Row>
          <TouristSingleResortHeader />
        </Row>
        <Row style={{ marginTop: "60px" }}>
          <TouristSingleResortLightBox />
        </Row>
        <Row style={{ marginTop: "60px" }}>
          <Col sm={12} lg={9}>
            <TouristSingleResortDescription />
          </Col>
          <Col sm={12} lg={3}>
            <TouristSingleResortAside />
          </Col>
        </Row>
        <Row style={{ marginTop: "60px" }}>
          <TouristSingleResortMap />
        </Row>
      </Container>
    </div>
  );
};

export default TouristSingleResortPage;
